const imgUrl = require('~/assets/mining.png')

class Child{
    constructor ({draw,  x, y, r, }) {
        this.draw = draw
        this.x = x
        this.y = y
        this.r = r
        this.instance
        
        this.init()
        
    }

    init () {
        const circle = this.draw.circle(this.r)
        circle.style({ 'stroke-width': '5',fill: '#fff',}).move(this.x, this.y)
        
        this.instance = {
            node: circle,
            length:  Math.PI * 2 * this.r
        }
    }

    _drawCountDown (stroke, duration) {
        const instance = this.instance
        const node = instance.node
        const x = node.cx()
        const y = node.cy()
        const countDown = this.draw.circle(this.r - 30)
        const pathLength =  Math.PI * 2 * (this.r - 30)
        
        countDown.attr('stroke-dasharray', pathLength).style({fill: '#fff', 'stroke-width': 3, 'stroke-dashoffset': pathLength, stroke})
        countDown.center(x, y).animate(duration).style({'stroke-dashoffset': 1})
        return countDown
        
    }

    //绘制倒计时
    drawCountDown() {
        const instance = this.instance
        const state = instance.data.state

        this.removeCountDown()
        if (state == 'follower'){
            instance.countDown = this._drawCountDown('#000', instance.data.elapsed * 1000 + 1000)
        } else if (state == 'candidate'){
            instance.countDown = this._drawCountDown('#f00', instance.data.elapsed * 1000 + 1000)
        } else if(state == 'leader'){
            instance.countDown = this._drawCountDown('#f00', instance.data.life * 1000)
        }
    }

    //移除已有的倒计时
    removeCountDown () {
        const instance = this.instance
        if (instance.countDown) {
            instance.countDown.remove()
        }
    }

    drawText () {
        const instance = this.instance
        if (instance.text) instance.text.remove();

        const x = instance.node.x()
        const y = instance.node.cy()
        const data = instance.data

        const text = this.draw.text(add => {
            add.tspan(data.server_name).newLine()
            add.tspan(data.state).newLine()
            add.tspan(`倒计时：${data.elapsed}`).newLine()
            add.tspan(`行为: ${data.loop}`).newLine()
        })

        text.move(x, y + this.r )
        instance.text = text
    }

    startMining () {
        this.endMining()

        const instance = this.instance
        const x = instance.node.cx()
        const y = instance.node.cy()
        const img = this.draw.image(imgUrl, 25, 25)
        img.center(x, y)
        
        instance.startMining = img
    }

    endMining () {
        if (this.instance.startMining) {
            this.instance.startMining.remove()
        }
    }

    renderFollower () {
        const loop = this.instance.data.loop
        this.instance.node.attr({ stroke: '#333', 'stroke-dasharray': 1})
        this.instance.isLeader = false

        switch(loop) {
            case 'Heartbeat':
                this.drawCountDown()
                break;
            case 'append_entries':
                this.removeCountDown()
                break;
            case 'start_mining':
                this.startMining()
                break;
            case 'end_mining':
                this.endMining()
                break;
        }
    }

    renderCandidate () {
        this.instance.isLeader = false
        this.instance.node.attr({ stroke: '#f00',  'stroke-dasharray': 3})
        
        if (this.instance.data.elapsed > 0) {
            this.drawCountDown()
        }
        
    }

    renderLeader() {
        if (!this.instance.isLeader) {
            this.drawCountDown()
            this.instance.node.attr({stroke: '#f00', 'stroke-dasharray': this.instance.length})
            this.instance.isLeader = true
        }
    }

    render(data) {
        const state = data.state
        this.drawText()
        if (state == 'follower') {
            this.renderFollower()
        } else if (state == 'candidate'){
            this.renderCandidate()
        }else if (state == 'leader'){
            this.renderLeader()
        }
    }

    change (data) {
        this.instance.data = data
        this.render(data)
    }
}

export default Child