<template>
    <div id="app">
    </div>
</template>

<script>
import SVG from 'svg.js'
import Animation from '~/js/animation'


export default {
    name: 'app',
    components: {
        
    },
    created () {
        
    },
    mounted () {
        const draw = SVG('app').size('100%', '1000px')
        const ani = new Animation(draw)
        
        const start = () => {
            const ws = new WebSocket('ws://10.0.248.211:8081', 'echo-protocol')

            ws.addEventListener('message', (ev) => {
                ani.change(ev.data)
            }, false)

            ws.addEventListener('close', (ev) => {
                console.log('close !')
                console.log(ev)

                start()
            }, false)

            ws.addEventListener('error', (ev) => {
                console.log('error !')
                console.log(ev)
            }, false)
        }

        start()
        
        window.ani = ani
    }
}
</script>

<style lang="scss">
#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    
}
