import Child from './child'

class Animation {
    constructor (draw) {
        this.draw = draw
        this.nodes = {}
        this.idx = 1
        this.r = 100
        
    }
    add (data) {
        let xIdx = this.idx % 5
        if (xIdx == 0) {
            xIdx = 5
        }
        this.nodes[data.server_name] = new Child({
            draw: this.draw,
            x: this.r * 2 * (xIdx + 1),
            y: (Math.ceil(this.idx / 5) - 1 ) * 300 + 20,
            r: this.r
        })

        this.idx += 1
        
        this.dispatch(data)
    }

    dispatch (data) {
        this.nodes[data.server_name].change(data)
    }

    change (dataString) {
        const data = JSON.parse(dataString)

        if (this.nodes[data.server_name]) {
            this.dispatch(data)
        }else{
            this.add(data)
        }
    }
}
 export default Animation